/* Estilos do CTA (Call to Action) */
.cta-section {
  background-color: #800020;
  color: #ffffff;
  padding: 4rem 0;
  text-align: center;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
}
.cta-section h2 {
  color: #ffd700;
  margin-bottom: 1.5rem;
  font-size: 2rem;
  font-weight: 700;
}
.cta-section p {
  margin-bottom: 2rem;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}
.cta-section .btn {
  margin-top: 1rem;
  display: inline-block;
  padding: 0.75rem 2rem;
  background-color: #ffd700;
  color: #800020;
  border: none;
  border-radius: 5px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
.cta-section .btn:hover {
  background-color: rgb(229.5, 193.5, 0);
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}